import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { NgForm } from '@angular/forms';
import { Usuarios } from '../../models/user.model';
import { UserService, MenuService } from '../../services/service.index';
import { Observable } from 'rxjs/Observable';

import { Router, ActivatedRoute } from '@angular/router';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['../login/login.component.css']
})
export class ResetPasswordComponent implements OnInit {
event = document.createEvent('UIEvents');
  emailValidate: string;
  email: string;
  buttonReset: boolean = true;
  constructor(@Inject(DOCUMENT)
  private _document,
  public _userService: UserService,
  public router: Router,
  public alert: ToastsManager,
  public _menu: MenuService
  ) {}

  ngOnInit() {
    document.getElementById('lodinaaaa').remove()
    this._menu.removePreload();
  }



  esEmailValido(email: string): boolean {
    let mailValido = false;
      'use strict';

      let EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

      if (email.match(EMAIL_REGEX)) {
        mailValido = true;
      }
    return mailValido;
  }
  resetPasswod(forma: NgForm) {
    if (forma.invalid) {
      return;
    }
    let email = forma.value.txtEmail;
    if (!this.esEmailValido(email)) {

      return;
    }
    this.buttonReset = false;
    this._userService.resetPassword(email).subscribe((resp: any) => {

      // swal('Attention!', 'To reset your password you must open the email and update it', 'info');
      this.alert.info('Attention!', 'To reset your password you must open the email and update it');


      this.router.navigate(['ok', email]);
    });
  }
}



