import { Component, OnInit } from '@angular/core';
import { EnrolmentService, NewEnrolmentService } from '../../services/service.index';
import { UserService } from '../../services/user/user.service';
import { CoursesService } from '../../services/courses/courses.service';
import { Courses } from '../../models/course.model';
import { Usuarios } from '../../models/user.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { EnrolmentModel } from '../../models/enrolment.model';

@Component({
	selector: 'app-enrolment-form',
	templateUrl: './enrolment-form.component.html',
	styles: []
})
export class EnrolmentFormComponent implements OnInit {
	forma: FormGroup;
	_Courses: Courses;
	studen: Usuarios;
	constructor(
		public _modal: EnrolmentService,
		public _studenSevice: UserService,
		public _courseService: CoursesService,
		public _serviceEnrolment: NewEnrolmentService
	) { }

	ngOnInit() {
		this.forma = new FormGroup({
			Student_id: new FormControl(null, Validators.required),
			course_id: new FormControl(null, Validators.required),
		});
		this.listCourses();
		this.listStuden();
	}
	ocultarModal() {
		this._modal.ocultarModal();
	}
	listStuden() {
		this._studenSevice.showAllStudent().subscribe((resp: any) => this.studen = resp.data);
	}
	listCourses() {
		this._courseService.showAll().subscribe((resp: any) => this._Courses = resp.data);
	}
	addEnrolment() {
		if (this.forma.invalid) {
			return;
		}
		let inscription = new EnrolmentModel(this.forma.value.Student_id, this.forma.value.course_id);
		this._serviceEnrolment.add(inscription).subscribe(resp => {
			if (resp) {
				this.ocultarModal();
				this._modal.eventUpdate.emit(resp);
			}
		});
	}
	// (click)="subirImgen()" [disabled]="!imagenTemporal"
}



