import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class EnrolmentService {
	public tipo: string;
	public id: string;
	public oculto: string = 'oculto';
	eventUpdate = new EventEmitter<any>();
	constructor() { }
	ocultarModal() {
		this.tipo = '';
		this.id = '';
		this.oculto = 'oculto'
	}
	mostratModal() {
		// this.tipo = type;
		// this.id = id;
		this.oculto = '';
	}

}



