import { Pipe, PipeTransform } from '@angular/core';
import { URL_SERVICES, imagen } from '../../config/config';

@Pipe({
  name: 'imagen'
})
export class ImagenPipe implements PipeTransform {

  transform(img: string, type: string = 'users'): any {
    let url = `${imagen}/`;

    if(!img){
      return `${imagen}/djzBuvhdBqhEqsthQf815aRMGrU8WR.jpg`;
    }else if(img == 'null'){
      if(type === 'users'){
        return `${imagen}/user-default.png`;
      }
      return `${imagen}/djzBuvhdBqhEqsthQf815aRMGrU8WR.jpg`;

    }
    if(img.indexOf('https')>= 0){
      return img;
    }
    switch (type) {
      case 'categories':
        url+=`category/${img}`;
      break;
      case 'users':
        url+=`users/${img}`;
      break;
      case 'courses':
        url+=img;
      break;
      default:
        console.log('type the img not exits');
        return `${imagen}/djzBuvhdBqhEqsthQf815aRMGrU8WR.jpg`;
    }
    return url;
  }

}



