import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EnrolmentModel } from '../../models/enrolment.model';
import { URL_SERVICES } from '../../config/config';
import { ToastsManager } from 'ng2-toastr';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';

@Injectable()
export class NewEnrolmentService {

	constructor(
		public alert: ToastsManager,
		public http: HttpClient,
		public router: Router,
	) { }
	add(enrlment: EnrolmentModel) {
		let url = `${URL_SERVICES}/Transactions`;
		return this.http.post(url, enrlment)
		.map((resp: any) => {
			console.log(resp);
			this.alert.success('The student has been enrolled in the course', 'Excellent');
			return true;
		})
		.catch(( err: any ) => {
			if(err.error.error == 'you_are_already_registered'){
				this.alert.error("can't add", 'you already have an assigned course');
			}
			console.log(err);
			return Observable.throw(err);
		});
	}
	list() {
		let url = `${URL_SERVICES}/Transactions`;
		return this.http.get(url);
	}
}



