import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import swal from 'sweetalert';
import { UserService, ModalUploadService, ArchivosService } from '../../services/service.index';
import { Usuarios } from '../../models/user.model';
import { ToastsManager } from 'ng2-toastr';

@Component({
	selector: 'app-modal',
	templateUrl: './modal.component.html',
	styles: []
})
export class ModalComponent implements OnInit {
	imagenSubir: File;
	forma: FormGroup;
	imagenTemporal: string | ArrayBuffer;
	Users: Usuarios;
	constructor(
		public _userService: UserService,
		public _Modalservicas: ModalUploadService,
		public _archivoService: ArchivosService,
		public alert: ToastsManager
	) {
		_archivoService.resetVariable();
	}

	ngOnInit() {
	}


	ocultarModal() {
		this.imagenSubir = null;
		this.imagenTemporal = null;
		this._Modalservicas.OcultarModal();
	}


	modalimagen(event: File) {
		if (!event) {
			this.imagenSubir = null;
			return;
		}

		this._archivoService.selectImg(event);
	}
	subirImgen() {
		this._archivoService.sublirArchivo(this._Modalservicas.tipo, this._Modalservicas.id)
			.then((resp: any) => {
				console.log(resp);
				this._Modalservicas.OcultarModal();
				this._userService.guardarStorage(resp.data, 'jnajsnd');
				this._userService.UserData = resp.data;
				this.alert.success('update', 'updated image');
			})
			.catch(err => console.log(err));
	}
	delete() {
		if (this._Modalservicas.tipo == 'Instructors') {
			// this._
			this._userService.Destroy(this._Modalservicas.id).subscribe(resp => {
				this._Modalservicas.deleteNotification.emit(resp);
				this._Modalservicas.OcultarModal();
			})
		}
		if (this._Modalservicas.tipo == 'Students') {

			this._userService.Destroy(this._Modalservicas.id).subscribe(resp => {
				this.alert.success('Students Deleted', 'with success');
				this._Modalservicas.deleteNotification.emit(resp);
				this._Modalservicas.OcultarModal();
			})

		}
	}
}



