import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { PAGES_ROUTES } from './pages.route';

import {SharedModule} from '../shared/shared.module';
import { ComponentModule } from '../component/component.module';

import { ChartsModule } from 'ng2-charts';
import {ToastModule} from 'ng2-toastr/ng2-toastr';
import { TagInputModule } from 'ngx-chips';
import { IconPickerModule } from 'ngx-icon-picker';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';

import { PipesModule } from '../pipes/pipes.module';

import { InitPages } from './pages-init/init.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CategoriesComponent } from './categories/categories.component';
import { CoursesComponent } from './courses/courses.component';
import { MyprofileComponent } from './myprofile/myprofile.component';
import { CategoryaddComponent } from './categoryadd/categoryadd.component';
import { EditTeacherComponent } from './edit-teacher/edit-teacher.component';
import { CourseAddComponent } from './course-add/course-add.component';
import { CrudUsersComponent } from './crud-users/crud-users.component';
import { PrivacySettingComponent } from './privacy-setting/privacy-setting.component';
import { AccountSettingComponent } from './account-setting/account-setting.component';
import { AddUserComponent } from './crud-users/add-user.component';
import { ReportComponent } from './report/report.component';
import { EnrolmentComponent } from './enrolment/enrolment.component';
import { MessageComponent } from './message/message.component';
import { ViewsCoursesComponent } from './courses/views-courses/views-courses.component';
import { RegisteredCoursesComponent } from './subcomponet/registered-courses/registered-courses.component';
import { SearchComponent } from './search/search.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    PAGES_ROUTES,
    ChartsModule,
    IconPickerModule,
    AngularMultiSelectModule,
    PipesModule,
    BrowserAnimationsModule,
    TagInputModule,
    ToastModule.forRoot(),
    ComponentModule
  ],
  declarations: [
    InitPages, DashboardComponent,
    CategoriesComponent, CoursesComponent, MyprofileComponent,
    CategoryaddComponent, EditTeacherComponent, CourseAddComponent,
    CrudUsersComponent, PrivacySettingComponent,
    AccountSettingComponent, AddUserComponent,
    ReportComponent, EnrolmentComponent,
    MessageComponent,
    ViewsCoursesComponent,
    RegisteredCoursesComponent,
    SearchComponent,
    ],
  exports: [
    InitPages,
    DashboardComponent
  ],
  providers: []

})
export class PagesModule { }



