import { Injectable, EventEmitter } from '@angular/core';
import { Category } from '../../models/category.model';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { URL_SERVICES } from '../../config/config';
import { Observable } from 'rxjs/Observable';
import swal from 'sweetalert';
import { ToastsManager } from 'ng2-toastr';
@Injectable()
export class CategoryService {
  public eventUpdateCategory = new EventEmitter<any>();
  
  constructor(public http: HttpClient, public router: Router,
    public alert: ToastsManager
    ) { }
  addCategoty(newCategory: Category) {

    let url = `${URL_SERVICES}/categories`;
    return this.http.post(url, newCategory).catch(erro => {
      let er = erro.error.error;
      if (er.name == 'The name has already been taken.') {
         this.alert.error('The name already exists.', 'Error')
      }
      // if (er.name) {
      //   return this.alert.info('The name is necessary');
      // }
      if (er.codecategory) {
         this.alert.error('el codecategory es necesario');
        // swal('error', '', 'error');
      }
      if (er.description){
        swal('error', 'el description es necesario', 'error');

      }

      return Observable.throw(erro);
    });
  }

  listCategory(){
    let url = `${URL_SERVICES}/categories`;
    return this.http.get(url).map((resp:any)=>{
        console.log(resp.data);
        
      return resp;
    });
  }

  deleteCategory(id){
    let url = `${URL_SERVICES}/categories/${id}`;
    return this.http.delete(url).catch((err:any) => {
      return Observable.throw(err);
    });
  }

  editCategory(id, category: Category){
    let url = `${URL_SERVICES}/categories/${id}`;
    return this.http.put(url, category).catch((err:any)=> {
      return Observable.throw(err);
    });

  }
  showOne(id){
    let url = `${URL_SERVICES}/categories/${id}`;
    return this.http.get(url, id)

  }
}



