import { Injectable, EventEmitter} from '@angular/core';

@Injectable()
export class ModalUploadService {
	public tipo: string;
	public id: string;
	public oculto: string = 'oculto';
	public oculdeteUser: string = 'oculto';
	public notificacion = new EventEmitter<any>();
	public deleteNotification = new EventEmitter<any>();
	constructor() {}
 

	 OcultarModal() {
		this.oculto = 'oculto';
		this.oculdeteUser = 'oculto';
		this.id = null;
		this.tipo = null;
	 }
	 modtrarModal(type, id) {
		this.oculto = '';
		this.id = id;
		this.tipo = type;
		console.log(id);

	 }
	 mostraModalUser(id, typo) {
		 console.log(id, typo)
		this.oculdeteUser = '';
		this.id = id;
		this.tipo = typo;
	 }
}



