import { Component, OnInit, ViewChild } from '@angular/core';
import { MenuService, UserService } from '../../services/service.index';
import { Chart } from 'chart.js';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styles: []
})
export class ReportComponent implements OnInit {
  xxx: any;
  @ViewChild('lineChart') private chartRef;
  chart: any;
  dataFirst = {
    label: "Student",
    data: [0, 59, 75, 20, 20, 55, 40],
    borderColor: '#00AEFF',

    // Set More Options
  };
  dataSecond = {
    label: "Instructor",
    data: [20, 15, 60, 60, 65, 30, 70],
    borderColor: '#221aec',

    // Set More Options
  };
  Courses = {
    label: "Courses",
    data: [2, 22, 3, 60, 10, 23, 3],
    borderColor: '#727834',

    // Set More Options
  };
  constructor(
    public menu: MenuService,
    public _UserServices: UserService
    ) { }

  ngOnInit() {
    this.menu.removePreload();
    console.log('sdasd');
    var speedData = {
      labels: ['January', 'February', "March", "April", "April", "June", "July", "August "],
      datasets: [this.dataFirst, this.dataSecond, this.Courses]
    };

    this.grafica(speedData)
    
  }
  grafica(speedData) {
    this.chart = new Chart(this.chartRef.nativeElement, {
      type: 'line',
      data: speedData,
      options: {
        legend: {
          display: true
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true
          }],
          ticks: {
            suggestedMin: 2,
            suggestedMax: 12
          }
        }
      }
    });
  }
}



