import { Component, OnInit, Inject } from '@angular/core';

import { UserService, MenuService } from '../../services/service.index';
import {Usuarios} from '../../models/user.model';
import { DOCUMENT } from '@angular/platform-browser';
declare function init();

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styles: []
})
export class DashboardComponent implements OnInit {
  studen = [];
  teacher = [];
  users = [];
  chart = [];
  chart2 = [];
  event = document.createEvent('UIEvents');
  constructor(
    public _serviceUSers: UserService,
    public _menu: MenuService,
    @Inject(DOCUMENT) public _document
    ) { }

  ngOnInit() {
    this._menu.removePreload();
    init();
    this._serviceUSers.cargarUsuarios().subscribe((resp: any) => {
      this.users = resp.data;
    });

  }


}



