import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { PipesModule } from '../pipes/pipes.module';

import { SubCategoryComponent } from './sub-category/sub-category.component';
import { ModalComponent } from './modal/modal.component';
import { EnrolmentFormComponent } from './enrolment-form/enrolment-form.component';
import { ServicesModule } from '../services/services.module';
import { MapaComponent } from './mapa/mapa.component';


@NgModule({
  imports: [
    CommonModule,
    ServicesModule,
    PipesModule,
    FormsModule,
    ReactiveFormsModule
  ],
  declarations: [SubCategoryComponent, ModalComponent, EnrolmentFormComponent, MapaComponent ],
  exports: [SubCategoryComponent, ModalComponent, EnrolmentFormComponent, MapaComponent ]
})
export class ComponentModule { }



