import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { URL_SERVICES } from '../../config/config';
// tslint:disable-next-line:import-blacklist
import { Observable } from 'rxjs';
import { SubCategoryModel } from '../../models/subCategory.model';

@Injectable()
export class SubCategoryService {
	url = `${URL_SERVICES}/subCategory`;
	public tipo: string;
	public id: any;
	public idParent: string;
	public oculto: string = 'oculto';
	public Deleteoculto: string = 'oculto';
	public notificacion = new EventEmitter<any>();
	public oneSubCategory = new EventEmitter<any>();
	public oneSubCategory1 = new EventEmitter<any>();
	public eventEdit = new EventEmitter<any>();
	public newSubCategory = new EventEmitter<any>();
	constructor(public http: HttpClient) { }


	showOne(id) {
		let url = `${this.url}/${id}`;
		return this.http.get(url).catch((err: any) => {
			return Observable.throw(err);
		});
	}
	add(data: SubCategoryModel) {
		return this.http.post(this.url, data).catch(err => {
			console.log(err);
			return Observable.throw(err);
		});
	}
	showAll() {
		return this.http.get(this.url).catch((err: any) => {
			console.log(err);
			return Observable.throw(err);
		});
	}
	edit(data: SubCategoryModel, id = this.id) {
		let url = `${this.url}/${id}`;
		return this.http.put(url, data).catch(err => {
			return Observable.throw(err);
		});
	}

	destroid(id) {
		let url = `${this.url}/${id}`;
		return this.http.delete(url).catch((err: any) => {
			console.log('subvategory ', err);
			return Observable.throw(err);
		});
	}



	ocultarModal() {
		this.oculto = 'oculto';
		this.Deleteoculto = 'oculto';
		this.id = null;
		this.idParent = null;

	}
	mostrarModall(id, idParent = 'nuevo') {
		this.id = id;
		this.idParent = idParent;
		this.oculto = '';
		this.showOne(id).subscribe((resp: any) => {
			this.notificacion.emit(resp.data);
		});
	}
	mostrarModalldelete(id) {
		this.id = id;
		this.Deleteoculto = '';
		this.showOne(id).subscribe((resp: any) => {
			this.oneSubCategory.emit(resp.data);
		});
	}
	mostrarModallnew() {
		this.id = 'nuevo';

		this.oculto = '';
	}
}



