import { Component, OnInit } from '@angular/core';
import { CategoryService, ModalUploadService, SubCategoryService} from '../../services/service.index';

import { Category } from '../../models/category.model';
declare var swal: any;
@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styles: []
})
export class CategoriesComponent implements OnInit {
  category =   [];
  constructor(
    public _categeryService: CategoryService,
    public _modalService: ModalUploadService,
    public _subCategory: SubCategoryService
    ) {
      _subCategory.oneSubCategory1.subscribe(resp => {
      this.cargarCategory();

      });
      _subCategory.newSubCategory.subscribe(resp => {
    this.cargarCategory();

      })
    this.cargarCategory();

   }

  ngOnInit() {
    this._categeryService.eventUpdateCategory.subscribe(resp=>{
      this.cargarCategory();
    })
    this._subCategory.eventEdit.subscribe(resp => {
      this.cargarCategory();
    });
  }
  cargarCategory() {
    this._categeryService.listCategory().subscribe((resp: any) => {
      this.category = resp.data;
    });
  }


  deleteCategory(id: number) {
    swal({
      title: 'Are you sure?',
      text: 'this category will be deleted',
      icon: 'warning',
      buttons: true,
      dangerMode: true
    }).then((resp) => {

      if (resp) {
        this._categeryService.deleteCategory(id).subscribe(respI => {
          this.cargarCategory();
        });
        swal(
          'Deleted!',
          'Your file has been deleted.',
          'success'
        );
      }
    });
  }
  deleteSubCategory(id) {
  this._subCategory.mostrarModalldelete(id);
  }
  editSubCategory(id, parent) {
    this._subCategory.mostrarModall(id, parent);
  }
}



