import { Injectable } from '@angular/core';
import { Courses } from '../../models/course.model';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { URL_SERVICES } from '../../config/config';
import { Observable } from 'rxjs/Observable';
import { ToastsManager } from 'ng2-toastr';

@Injectable()
export class CoursesService {

	constructor(public http: HttpClient, public router: Router, public alet: ToastsManager) { }

	addCourse(course: Courses) {

		let url = `${URL_SERVICES}/cursos`;
		return this.http.post(url, course).catch((err: any) => {
			if (err.status < 500) {
				if (err.error.error.Course_title) {
					this.alet.info('', err.error.error.Course_title[0]);
				}

				if (err.error.error) {

				}
			}

			return Observable.throw(err);

		});
	}

	listCourses(desde: number = 1, filter: any) {
		console.log(desde);

		let url = `${URL_SERVICES}/showCourses?page=${desde}`;
		return this.http.get(url, { params: filter }).map((resp: any) => {

			resp.data.path = null;
			return resp.data;
		}).catch((err: any) => {
			console.log(err);

			return Observable.throw(err);

		});
	}

	deleteCourse(id) {
		let url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.delete(url).catch((err) => {
			return Observable.throw(err);

		})

	}
	showOne(id) {
		let url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.get(url)
	}
	editCourses(id, course: Courses) {


		let url = `${URL_SERVICES}/cursos/${id}`;
		return this.http.put(url, course).catch(err => {

			this.alet.error(err.error.error)
			return Observable.throw(err);
		});
	}
	showAll() {
		let url = `${URL_SERVICES}/cursos`;
		return this.http.get(url).catch((err: any) => {
			return Observable.throw(err);

		});
	}
	showPDF() {
		let url = `${URL_SERVICES}/download_pdf`;
		window.location.href = url;
	}
	showExcel() {
		let url = `${URL_SERVICES}/download_excel`;
		window.location.href = url;
	}
}



