import { Component, OnInit } from '@angular/core';
import { MenuService, UserService } from '../../services/service.index';
import { Usuarios } from '../../models/user.model';
import { Subscription } from 'rxjs';
import 'rxjs/add/operator/takeUntil';
import { ModalUploadService } from '../../component/modal/modal-upload.service';
import { ToastsManager } from 'ng2-toastr';

@Component({
	selector: 'app-crud-users',
	templateUrl: './crud-users.component.html',
	styles: []
})
export class CrudUsersComponent implements OnInit {
	teacher: Subscription;
	student: Subscription;
	getData: any = [];
	user: Usuarios[] = [];
	loading: boolean = true;
	arrayFilter = { name: '', lastname: '', verified: ''};

	activeLessons: boolean = false;
	activeSections: boolean = false;
	activePeriod: boolean = true;
	activeStatus: boolean = true;
	activeTeacher: boolean = false;
	activeCategory: boolean = false;
	activePrice: boolean = false;
	activeEnrolments: boolean = false;
	activePublished: boolean = false;

	ColumnStatus: boolean = true;
	ColumnName: boolean = true;
	ColumnAuthor: boolean = true;
	ColumnCategory: boolean = true;
	ColumnPrice: boolean = true;
	ColumnLessons: boolean = true;
	ColumnSections: boolean = true;
	ColumnDemography: boolean = true;
	ColumnEnrolments: boolean = true;
	ColumnPublished: boolean = true;
	desdeStudent: number = 1;
	desdeInstructor: number = 1;
	// ____________________________________________
	current_page: number;
	total_pages: number;
	next_page: number;
	prev_page: number;
	totalRegister: number;
	nextPege: number;
	
	constructor(
		public _headerService: MenuService,
		public _userService: UserService,
		public _modalService: ModalUploadService,
		public alert: ToastsManager
	) {
		_headerService.getDataRouter()
			.subscribe((resp: any) => {
				this.getData = resp;
				if (resp.sub === 'teacher') {
					this.user = null;
					this.showTeacher();
				}
				if (resp.sub === 'studen') {
					this.user = null;
					this.showStudent();
				}
			});

	}

	ngOnInit() {
		this._modalService.deleteNotification.subscribe(esp => {
			if (this.getData.title == 'Instructors') {
				this.showTeacher();
			} else if (this.getData.title == 'Students') {
				this.showStudent();
			}
		})
		this._headerService.removePreload();
	}

	showTeacher() {
		this.loading = true;
		this._userService.allTeacher(this.desdeInstructor, this.arrayFilter).subscribe((resp: any) => {
			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page
			this.total_pages = resp.last_page;
			this.user = resp.data;
			this.loading = false
		});
	}
	
	showStudent() {
		this.loading = true;
		this._userService.allStudents(this.desdeStudent, this.arrayFilter).subscribe((resp: any) => {
			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page
			this.total_pages = resp.last_page;
			this.user = resp.data;
			this.loading = false
		});
	}

	deleteUser(id) {

		if (this._userService.UserData.id == id) {
			this.alert.info('cannot be removed', 'It`s not valid');
			return;
		}
		this._modalService.mostraModalUser(id, this.getData.title);
	}
	funtionDesde(value) {
		if(this.getData.title == 'Students') {
			if (this.desdeStudent < 1) {
				return this.desdeStudent = 1
			}
			this.desdeStudent += value;
			this.showStudent();
		}else if (this.getData.title == 'Instructors'){
			if (this.desdeInstructor < 1) {
				return this.desdeInstructor = 1;
			}
			this.desdeInstructor += value;
			this.showTeacher();
		}
	}

	updateFilterStudemt(event, type){
		let val = event.toLowerCase();
		if (type == 'name') {
			this.arrayFilter.name = val;
			this.arrayFilter.lastname = val;
		}
		if (type == 'verified') {
			this.arrayFilter.verified = val;
		}
		this.showStudent();
		
	}
	updateFilterTeacher(event, type) {
		let val = event.toLowerCase();
		if (type == 'name') {
			this.arrayFilter.name = val;
		}
		if (type == 'lastname') {
			this.arrayFilter.lastname = val;
		}
		if (type == 'verified') {
			this.arrayFilter.verified = val;
		}
		this.showTeacher();
		console.log('teacher');
	}
}



