import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { ToastModule } from 'ng2-toastr';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';


import {
	UserService,
	LoginGuardGuard,
	CategoryService,
	CoursesService,
	ArchivosService,
	ModalUploadService,
	MenuService,
	EnrolmentService,
	SubCategoryService,
	NewEnrolmentService
} from './service.index';


// _________________________________________________________________________
@NgModule({
	imports: [
		HttpClientModule,
		BrowserModule,
		BrowserAnimationsModule,
		ToastModule.forRoot()
	],
	providers: [
		UserService,
		LoginGuardGuard,
		CoursesService,
		CategoryService,
		ArchivosService,
		ModalUploadService,
		MenuService,
		EnrolmentService,
		SubCategoryService,
		NewEnrolmentService
	],
	declarations: []
})
export class ServicesModule { }



