import { Component, OnInit, ViewContainerRef } from '@angular/core';
import {  NgForm } from '@angular/forms';
import { UserService, MenuService } from '../../services/service.index';
import { Usuarios } from '../../models/user.model';
import { Router } from '@angular/router';
import swal from 'sweetalert';
import { Observable } from 'rxjs/Observable';
import { ToastsManager } from 'ng2-toastr';

declare function initLoader();

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

event = document.createEvent('UIEvents');

recuerdame: boolean = false;
email: string;
emailValidate: string;
  constructor(
  public _serviceUser: UserService,
  public router: Router,
  public toastr: ToastsManager,
  public _menu: MenuService
  ) { }
  ngOnInit() {
    this._menu.removePreload();
    document.getElementById('lodinaaaa').remove()
     this.email = localStorage.getItem('email') || '';
    this.emailValidate = this.email;
    if (this.email.length > 1) {
      this.recuerdame = true;
    }

  }
  loginSession(forma: NgForm) {

    if (forma.invalid) {
      return;
    }

    let user = new Usuarios(null , null, forma.value.email, forma.value.password, null, null);
    this._serviceUser.initSession(user, forma.value.condicions).catch((err: any) => {

      if (err.statusText == 'Unknown Error') {
          this.toastr.error('Error', 'The server is down');
          return;
      }
      if(err.statusText =="Internal Server Error"){
        this.toastr.error('Error', 'Base de datos deconectada');
        return
      }


      return Observable.throw(err);
    }).subscribe(resp => {
      if(resp == true) {
        // this.router.navigate(['/dashboard'])

        window.location.href= "/"
      }else{
        this.toastr.error('Error!', 'The credentials are incorrect');

      // swal('Error!', 'The credentials are incorrect', 'error')
      }

    });
  }


}



