import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {MenuService} from '../../services/menu/menu.service';
@Component({
  selector: 'app-alert-reset',
  templateUrl: './alert-reset.component.html',
  styleUrls: ['../login/login.component.css']
})
export class AlertResetComponent implements OnInit {
event = document.createEvent('UIEvents');

  email: string = '';
  constructor(
    public router: Router,
    public activtedRouter: ActivatedRoute,
    public _menu: MenuService
    ) {
    activtedRouter.params.subscribe((param) => {
      this.email = param.email;
    });
   }

  ngOnInit() {
    document.getElementById('lodinaaaa').remove()
    
    this._menu.removePreload();
    this.event.initEvent('resize', true, false);
    window.dispatchEvent(this.event);
  }
  sendEmail() {
    this.router.navigate['login'];
  }
}



