import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { CoursesService, CategoryService, UserService, MenuService } from '../../services/service.index';
import { Category } from '../../models/category.model';
import { Usuarios } from '../../models/user.model';
import { Router } from '@angular/router';

@Component({
	selector: 'app-courses',
	templateUrl: './courses.component.html',
	styles: []
})
export class CoursesComponent implements OnInit {
	rows_Course = [];
	loading:boolean = true;
	Total_lesson = [];
	Total_Enrolments = [];
	category: Category[] = [];
	users: Usuarios[] = [];
	Students: Usuarios[] = [];
	arrayFilter = { category: '', teacher: '', status: '', name: '', price: '' };
	columns = [];
	desde:number = 1;
	nextPege: number; 
	totalRegister: number;
	next_page: any;
	prev_page: any;
	current_page: any;
	total_pages: any;
	activeLessons: boolean = false;
	activeSections: boolean = false;
	activePeriod: boolean = true;
	activeStatus: boolean = true;
	activeTeacher: boolean = false;
	activeCategory: boolean = false;
	activePrice: boolean = false;
	activeEnrolments: boolean = false;
	activePublished: boolean = false;

	ColumnStatus: boolean = true;
	ColumnName: boolean = true;
	ColumnAuthor: boolean = true;
	ColumnCategory: boolean = true;
	ColumnPrice: boolean = true;
	ColumnLessons: boolean = true;
	ColumnSections: boolean = true;
	ColumnDemography: boolean = true;
	ColumnEnrolments: boolean = true;
	ColumnPublished: boolean = true;
	ColumnRevenue: boolean = true;
	ColumnsubCategory: boolean = false;
	constructor(
		public _coursesServices: CoursesService,
		public _userService: UserService,
		public _categoryService: CategoryService,
		public router: Router,
		public _modalMap: MenuService
	) { }

	ngOnInit() {
		this.ListCourses();
		this._categoryService.listCategory().subscribe((resp: any) => {
			console.log(resp.data);
			this.category = resp.data;
		});
		this._userService.cargarUsuarios().subscribe((resp:any) => {
			this.users = resp.data;
		})

	}

	updateFilter(event, type) {
		let val = event.toLowerCase();
		if (type == 'category') {
			this.arrayFilter.category = val;
		}
		if (type == 'status') {
			this.arrayFilter.status = val;
		}
		if (type == 'teacher') {
			this.arrayFilter.teacher = val;
		}
		if (type == 'price') {
			this.arrayFilter.price = val;
		}
		if (type == 'name') {
			this.arrayFilter.name = val;
		}
		console.log(this.arrayFilter);
		this.ListCourses();
	}

	deleteCourse(value) {
		this.loading = true;
		this._coursesServices.deleteCourse(value).subscribe(resp => {
			this.ListCourses();
			this.loading = false;
		});
	}

	ListCourses() {
		this.loading = true;
		this._coursesServices.listCourses(this.desde, this.arrayFilter).subscribe((resp: any) => {
			this.rows_Course = resp.data;
			console.log(resp.data);

			this.totalRegister = resp.total;
			this.nextPege = resp.last_page;
			this.next_page = resp.next_page_url;
			this.prev_page = resp.prev_page_url;
			this.current_page = resp.current_page;
			this.total_pages = resp.last_page;
			this.loading = false;
		})
	}



	views() {
		this._modalMap.mostraModal();
	}
	cambiarDesde(value: number) {

		this.desde += value;
		if(this.desde < 1) {
			this.desde = 1;
		}
		this.ListCourses();

	}
	GenerarAchivo(type) {
		if(type == 'PDF') {
			this._coursesServices.showPDF();
		}
		if(type == 'EXCEL') {
			this._coursesServices.showExcel();
		}
	}
}


