import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { UserService, MenuService } from '../../services/service.index';
import { Usuarios } from '../../models/user.model';
import { Router } from '@angular/router';
declare function init();
declare function initLoader();
declare function activeMap()

@Component({
	selector: 'app-init',
	templateUrl: './init.component.html',
	styles: []
})
export class InitPages implements OnInit {

	constructor(@Inject(DOCUMENT)
	private _document,
		public _userService: UserService,
		public _menu: MenuService,
		public router: Router
	) { }
	nameUser: string = '..';
	Users: Usuarios;
	searchInit: string;
	oculto=true;
	ngOnInit() {
		activeMap()
		initLoader();
		document.getElementById('inicio').remove();
		this._menu.removePreload();
		this.Users = this._userService.UserData;
		// this._document.oncontextmenu = function(){ return false; };
	}

	searchEmiter(value) {
		if(value.length == 0) {
			this.router.navigate(['/dashboard']);
		}else {
			this.router.navigate(['/search', value]);
		}
	}
}



